import org.w3c.dom.HTMLCanvasElement
import org.khronos.webgl.WebGLRenderingContext as GL
import vision.gears.webglmath.UniformProvider
import kotlin.js.Date
import vision.gears.webglmath.Vec3
import vision.gears.webglmath.Mat4
import kotlin.math.acos
import kotlin.math.cos
import kotlin.math.exp
import kotlin.math.PI
import kotlin.math.abs
import kotlin.math.sin


class Scene(val gl: WebGL2RenderingContext) : UniformProvider("scene") {

    val vsTrafo = Shader(gl, GL.VERTEX_SHADER, "shaders/trafo-vs.glsl")
    val vsShadow = Shader(gl, GL.VERTEX_SHADER, "shaders/shadow-vs.glsl")
    val fsSolid = Shader(gl, GL.FRAGMENT_SHADER, "shaders/solid-fs.glsl")
    val fsTextured = Shader(gl, GL.FRAGMENT_SHADER, "shaders/textured-fs.glsl")
    val shadowProgram = Program(gl, vsShadow, fsSolid)
    val texturedProgram = Program(gl, vsTrafo, fsTextured)
    val quadGeometry = TexturedQuadGeometry(gl)

    val gameObjects = ArrayList<GameObject>()
    val shadowedGameObjects = ArrayList<GameObject>()
    val camera = PerspectiveCamera(*Program.all)

    init {
        camera.position.set(0f, 100f, 0f)
    }

    // Gameobjects
    val chevyMaterial = Material(texturedProgram)
    val chevyMesh = MultiMesh(gl, "media/chevy/chassis.json", materials = *arrayOf(chevyMaterial))
    val wheelMesh = MultiMesh(gl, "media/chevy/wheel.json", materials = *arrayOf(chevyMaterial))
    val chevyObject = object : GameObject(chevyMesh) {
        init {
            m = 15f // mass
            v.set(Vec3(0f, 0f, 0f)) // velocity
            c = 15f // air-resistance coefficient
            c = 50f // air-resistance coefficient
            theta = 5f // moment of inertia
            omega = 0f// angular speed
        }

        val dir = Vec3(0f, 0f, 0f)
        var gear = 1f
        override fun move(t: Float, dt: Float, keysPressed: Set<String>, gameObjects: List<GameObject>): Boolean {

            // gear selection
            if (keysPressed.contains("1")) gear = 1f
            if (keysPressed.contains("2")) gear = 2f
            if (keysPressed.contains("3")) gear = 4f
            if (keysPressed.contains("4")) gear = 7f
            if (keysPressed.contains("5")) gear = 11f

            // let's start by zeroing out the forces and torques
            val F = Vec3(0f, 0f, 0f) // Force
            val Fsize = 600f * gear;

            var tau = 0f // Torque, always tangential
            val tausize = 1f * gear;


            // setting torque and force
            if (keysPressed.contains("LEFT") && tau > -0.01f)
                tau += 1;
            if (keysPressed.contains("RIGHT") && tau < 0.01f)
                tau -= 1;
            if (keysPressed.contains("UP")) {
                F += Vec3(1f, 0f, 0f) * sin(yaw)
                F += Vec3(0f, 0f, 1f) * cos(yaw)
            }
            if (keysPressed.contains("DOWN")) {
                F -= Vec3(1f, 0f, 0f) * sin(yaw)
                F -= Vec3(0f, 0f, 1f) * cos(yaw)
            }
            if (keysPressed.contains("SPACE")) {
                v /= 2f
                omega /= 2f
            }


            // setting speed and position
            F *= Fsize
            var a = F / m // acceleration
            v += a * dt
            position += v * dt

            // setting angular speed but not yet rotating
            tau *= tausize
            var beta = tau / theta // angular acceleration
            omega += beta * dt
            rotateWheelsVertically(omega * dt)

            // calculating the cars and its velocity's angle
            dir.set(Vec3(0f, 0f, 0f))
            dir += Vec3(1f, 0f, 0f) * sin(yaw)
            dir += Vec3(0f, 0f, 1f) * cos(yaw)
            var dirAngle = acos((v.x * dir.x + v.z * dir.z) / v.length() * dir.length())


            // the car starts turning only is it has at least a certain velocity
            if (v.length() >= 5f) {
                // going backwards means the car has reverse steering
                if (dirAngle < PI.toFloat() / 2)
                    yaw += omega * dt
                else
                    yaw -= omega * dt
            } else {
                omega = 0f
                //resetWheelsVertically(1.015f)
            }

            // going backwards means the wheels rotate backwards
            if (dirAngle < PI.toFloat() / 2)
                rotateWheelsHorizontally(v.length() / 3f * dt)
            else
                rotateWheelsHorizontally(v.length() / -3f * dt)

            // air resistance is slowing us down
            v *= exp(-dt * c / (gear / 2f) / m)
            omega *= exp(-dt * c2 * gear / theta)
            return true;
        }
    }

    // assistant function to reset the wheels when turning
    fun resetWheelsVertically(by: Float) {
        wheelObjectFL.yaw /= by
        wheelObjectFR.yaw /= by
    }

    // assistant function to rotate the wheels when turning
    fun rotateWheelsVertically(by: Float) {
        if (abs(wheelObjectFL.yaw) < PI.toFloat() / 6) {
            wheelObjectFL.yaw += by
            wheelObjectFR.yaw += by
        }
    }

    // assistant function to rotate the wheels when moving
    fun rotateWheelsHorizontally(by: Float) {
        wheelObjectFL.pitch += by
        wheelObjectFR.pitch += by
        wheelObjectRL.pitch += by
        wheelObjectRR.pitch += by
    }

    val wheelObjectFL = object : GameObject(wheelMesh, Vec3(0.4f, -0.25f, 0.875f) * 16f) {
        init {
            m = 15f // mass
            v.set(Vec3(0f, 0f, 0f)) // velocity
            c = 15f // air-resistance coefficient
            c2 = 15f // air-resistance coefficient
            theta = 5f // moment of inertia
            omega = 0f// angular speed
        }

        val parent = chevyObject
        override fun update() {
            modelMatrix.set().scale(scale).rotate(roll).rotate(pitch, 1.0f, 0.0f, 0.0f).rotate(yaw, 0.0f, 1.0f, 0.0f).translate(position)
            modelMatrix *= parent.modelMatrix
        }

        override fun move(t: Float, dt: Float, keysPressed: Set<String>, gameObjects: List<GameObject>): Boolean {
            if (keysPressed.contains("LEFT") && keysPressed.contains("RIGHT"))
                yaw = 0f
            if (!keysPressed.contains("LEFT") && !keysPressed.contains("RIGHT"))
                yaw *= exp(-dt * c2 / theta)
            return true;
        }
    }

    val wheelObjectFR = object : GameObject(wheelMesh, Vec3(-0.4f, -0.25f, 0.875f) * 16f) {
        init {
            m = 15f // mass
            v.set(Vec3(0f, 0f, 0f)) // velocity
            c = 15f // air-resistance coefficient
            c2 = 15f // air-resistance coefficient
            theta = 5f // moment of inertia
            omega = 0f// angular speed
        }

        val parent = chevyObject
        override fun update() {
            modelMatrix.set().scale(scale).rotate(roll).rotate(pitch, 1.0f, 0.0f, 0.0f).rotate(yaw, 0.0f, 1.0f, 0.0f).translate(position)
            modelMatrix *= parent.modelMatrix
        }

        override fun move(t: Float, dt: Float, keysPressed: Set<String>, gameObjects: List<GameObject>): Boolean {
            if (keysPressed.contains("LEFT") && keysPressed.contains("RIGHT"))
                yaw = 0f
            if (!keysPressed.contains("LEFT") && !keysPressed.contains("RIGHT"))
                yaw *= exp(-dt * c2 / theta)
            return true;
        }
    }

    val wheelObjectRL = object : GameObject(wheelMesh, Vec3(0.4f, -0.25f, -0.725f) * 16f) {
        val parent = chevyObject
        override fun update() {
            modelMatrix.set().scale(scale).rotate(roll).rotate(pitch, 1.0f, 0.0f, 0.0f).rotate(yaw, 0.0f, 1.0f, 0.0f).translate(position)
            modelMatrix *= parent.modelMatrix
        }
    }

    val wheelObjectRR = object : GameObject(wheelMesh, Vec3(-0.4f, -0.25f, -0.725f) * 16f) {
        val parent = chevyObject
        override fun update() {
            modelMatrix.set().scale(scale).rotate(roll).rotate(pitch, 1.0f, 0.0f, 0.0f).rotate(yaw, 0.0f, 1.0f, 0.0f).translate(position)
            modelMatrix *= parent.modelMatrix
        }
    }

    init {
        chevyMaterial["colorTexture"]?.set(Texture2D(gl, "media/chevy/chevy.png"))
        chevyObject.position.set(Vec3(48f, 7f, -119f));
        chevyObject.yaw = 3f * PI.toFloat() / 2f
        gameObjects.add(chevyObject)
        gameObjects.add(wheelObjectFL)
        gameObjects.add(wheelObjectFR)
        gameObjects.add(wheelObjectRL)
        gameObjects.add(wheelObjectRR)

        shadowedGameObjects.add(chevyObject)
        shadowedGameObjects.add(wheelObjectFL)
        shadowedGameObjects.add(wheelObjectFR)
        shadowedGameObjects.add(wheelObjectRL)
        shadowedGameObjects.add(wheelObjectRR)
    }

    // racetrack
    val trackMaterial = Material(texturedProgram)
    val raceTrackObject = GameObject(Mesh(trackMaterial, quadGeometry), Vec3(0f, 0.2f, 0f), 0f, 0f, PI.toFloat() / 2f, Vec3(600f, 600f, 0f))

    init {
        trackMaterial["colorTexture"]?.set(Texture2D(gl, "media/racetrack.png"))
        gameObjects.add(raceTrackObject)
    }

    // trees
    val treeMaterial = Material(texturedProgram)
    val smoothTreeMesh = MultiMesh(gl, "media/smoothtree.json", materials = *arrayOf(treeMaterial))
    val treeMesh = MultiMesh(gl, "media/tree.json", materials = *arrayOf(treeMaterial))

    init {
        treeMaterial["colorTexture"]?.set(Texture2D(gl, "media/tree.png"))
      val treeObject1 = GameObject(smoothTreeMesh, Vec3(-375f, 3f, 140f))
      val treeObject2 = GameObject(treeMesh, Vec3(305f, 3f, 202f))
      val treeObject3 = GameObject(smoothTreeMesh, Vec3(-69f, 3f, -237f))
      val treeObject4 = GameObject(smoothTreeMesh, Vec3(-370f, 3f, -502f))
      val treeObject5 = GameObject(smoothTreeMesh, Vec3(460f, 3f, -442f))

        gameObjects.add(treeObject1)
        gameObjects.add(treeObject2)
        gameObjects.add(treeObject3)
        gameObjects.add(treeObject4)
        gameObjects.add(treeObject5)
        shadowedGameObjects.add(treeObject1)
        shadowedGameObjects.add(treeObject2)
        shadowedGameObjects.add(treeObject3)
        shadowedGameObjects.add(treeObject4)
        shadowedGameObjects.add(treeObject5)
    }

    // Shawdows
    val shadowMaterial = Material(shadowProgram)
    val lightDir = Vec3(1f, -2f, -4f)
    val shadowMatrix by Mat4()

    init {
        shadowMaterial["color"]?.set(Vec3(0f, 0f, 0f))
        shadowMatrix.set(
                1f, 0f, 0f, 0f,
                -lightDir.x / lightDir.y, 0f, -lightDir.z / lightDir.y, 0f,
                0f, 0f, 1f, 0f,
                0f, 1f, 0f, 1f
        )

    }


    // infinite plane as ground
    val grassMaterial = Material(texturedProgram);
    val infiniteGeometry = InfiniteGeometry(gl)
    val infiniteObject = GameObject(Mesh(grassMaterial, infiniteGeometry))

    init {
        grassMaterial["colorTexture"]?.set(Texture2D(gl, "media/grass.png"))
        infiniteObject.scale *= 50f
    }

    // Skybox
    val boxTrafo = Shader(gl, GL.VERTEX_SHADER, "shaders/box-vs.glsl")
    val fsBox = Shader(gl, GL.FRAGMENT_SHADER, "shaders/box-fs.glsl")
    val boxProgram = Program(gl, boxTrafo, fsBox)
    val backgroundMaterial = Material(boxProgram)
    val envTexture = TextureCube(gl,
            "media/posx512.jpg",
            "media/negx512.jpg",
            "media/posy512.jpg",
            "media/negy512.jpg",
            "media/posz512.jpg",
            "media/negz512.jpg"
    )

    val backgroundObject = GameObject(Mesh(backgroundMaterial, quadGeometry))

    init {
        backgroundMaterial["envTexture"]?.set(this.envTexture)
        gameObjects.add(infiniteObject)
        gameObjects.add(backgroundObject)
    }


    init {
        gl.enable(GL.DEPTH_TEST)
        addComponentsAndGatherUniforms(*Program.all)
    }

    fun resize(gl: WebGL2RenderingContext, canvas: HTMLCanvasElement) {
        gl.viewport(0, 0, canvas.width, canvas.height)
        camera.setAspectRatio(canvas.width.toFloat() / canvas.height.toFloat())
    }

    // helicopter that the camera is attached to
    // always looks at car
    // also sets the camera
    fun moveCameraByCar(dt: Float, car: GameObject, camera: PerspectiveCamera) {
        val dir = Vec3(0f, 0f, 0f)
        dir += Vec3(1f, 0f, 0f) * sin(car.yaw)
        dir += Vec3(0f, 0f, 1f) * cos(car.yaw)
        dir += Vec3(0f, -0.5f, 0f)
        // the angle of the car and its velocity
        var dirAngle = acos((car.v.x * dir.x + car.v.z * dir.z) / (car.v.length() * dir.length()))

        dir.set(car.position - dir * 40f)
        val diff = dir - camera.position
        camera.position += diff * dt * 5f

        // if we're going backwards let's see backwards
        var yawDiff = 0f
        if (dirAngle > PI.toFloat() / 2)
            yawDiff = car.yaw - camera.yaw
        else
            yawDiff = car.yaw - camera.yaw + PI.toFloat()

        camera.yaw += yawDiff * dt * 5f
        camera.update()
    }


    var cameraSpectate = false
    val timeAtFirstFrame = Date().getTime()
    var timeAtLastFrame = timeAtFirstFrame

    var timeAtLastCamerButtonPress = timeAtFirstFrame

    @Suppress("UNUSED_PARAMETER")
    fun update(gl: WebGL2RenderingContext, keysPressed: Set<String>) {

        val timeAtThisFrame = Date().getTime()
        val dt = (timeAtThisFrame - timeAtLastFrame).toFloat() / 1000.0f
        val t = (timeAtThisFrame - timeAtFirstFrame).toFloat() / 1000.0f
        timeAtLastFrame = timeAtThisFrame

        if (keysPressed.contains("C") && cameraSpectate && Date().getTime() - timeAtLastCamerButtonPress > 300) {
            cameraSpectate = false
            moveCameraByCar(dt, chevyObject, camera)
            timeAtLastCamerButtonPress = Date().getTime()
        }
        if (keysPressed.contains("C") && !cameraSpectate && Date().getTime() - timeAtLastCamerButtonPress > 300) {
            cameraSpectate = true
            camera.position.set(0f, 100f, 0f)
            timeAtLastCamerButtonPress = Date().getTime()
        }

        if (!cameraSpectate) {
            camera.move(dt, keysPressed)
            moveCameraByCar(dt, chevyObject, camera)
        } else {
            camera.move(dt, keysPressed)
        }

        console.log(chevyObject.position)

        // clear the screen
        gl.clearColor(0.3f, 0.0f, 0.3f, 1.0f)
        gl.clearDepth(1.0f)
        gl.clear(GL.COLOR_BUFFER_BIT or GL.DEPTH_BUFFER_BIT)

        gameObjects.forEach { it.update() }
        gameObjects.forEach { it.move(t, dt, keysPressed, gameObjects) }


        shadowedGameObjects.forEach { it.using(shadowMaterial).draw(this, camera) }
        gameObjects.forEach { it.draw(camera) }
    }
}
